<template>
  <div>
    <div id="myPDF" style="height: 95%; width: 95%; margin: auto;"></div>
  </div>
</template>

<script>

export default {
  name: 'pdf',
  data(){
    return {

    }
  },
  created(){},
  mounted(){
    window.$("#myPDF").pdf({
      source: 'https://qinghuansmile.top/testCode/demo.pdf',
      loaded: this.loaded,
    });
  },
  // $(function () {
	
	// })
  methods:{
    loaded(){
      var $targetObj = window.$('#myPDF');
      window.cat.touchjs.init($targetObj, function (left, top, scale, rotate) {
        $targetObj.css({
          left: left,
          top: top,
          'transform': 'scale(' + scale + ') rotate(' + rotate + 'deg)',
          '-webkit-transform': 'scale(' + scale + ') rotate(' + rotate + 'deg)'
        });
      });
      console.log(window.cat);
      //初始化拖动手势
      window.cat.touchjs.drag($targetObj, function () {
        // console.log(left);
        // console.log(top);
      });
      //初始化缩放手势
      window.cat.touchjs.scale($targetObj, function () {
        // console.log(scale);
      });
      window.cat.touchjs.scale(window.$('canvas'), function () {
        // console.log(scale);
      });
    }
  }
}
</script>
